:root {
    --white: #E7E7E7;
    --dark-blue: #217CC7;
    --light-blue: #95E7E9;
    --yellow: #FEBB3B;
    --red: #FE767C;
    --black: #0E0E34;
    font-size: 24px;


}
body {
    color: #ff8859;
}

.container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: max-content;
    grid-template-areas: 
    "question question"
    "answers info"
    "answers correct"
    "answers correct"
    "answers correct"
    "navigation navigation";
    border: 2px solid green;
    margin: 5vw;


}

.question {
    grid-area: question;
    border: 1px solid magenta;
    margin-bottom: 1.2rem;
    background-color: var(--white);
    -webkit-box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.68); 
    box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.68);

}

.question h1 {
    color: var(--dark-blue);
    border: solid magenta 1px;
    margin: 0.8rem 0 0 0.8rem;
    font-size: 1rem;
}
.question p {
    color: var(--black);

}

.answers {

}

.answer {
    color: var(--black);
    height: fit-content;
    background-color: var(--white);
    -webkit-box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.68); 
    box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.68);
}
.answer span {
    color: var(--dark-blue);
}
.answer p {
    padding: 0.4rem 0.6rem 0.4rem 0.6rem;
}

.correct {
    
    color: var(--black);
    background-color: var(--white);
    -webkit-box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.68); 
    box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.68);
}
.correct span {
    color: var(--dark-blue);
}
.correct p {
    padding: 0.4rem 0.6rem 0.4rem 0.6rem;
}

.info {
    grid-area: info;
    border: solid magenta 1px;
    height: 100px;

}

.navigation {
    grid-area: navigation;

}

.forward {
     color: var(--black);
    width: 50%;
     height: fit-content;
    background-color: var(--white);
    -webkit-box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.68); 
    box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.68);
}

.back {
    width: 50%;
     color: var(--black);
    height: fit-content;
    background-color: var(--white);
    -webkit-box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.68); 
    box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.68);
}



